import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { Form, Input, Button, Card, message } from 'antd';
import { UserOutlined, LockOutlined, MailOutlined, BankOutlined } from '@ant-design/icons';

const Register = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const onFinish = async (values) => {
    try {
      setLoading(true);
      
      // API
      // API
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      message.success('');
      navigate('/login');
    } catch (error) {
      message.error(error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100">
      <Card className="w-96">
        <div className="text-2xl font-bold text-center mb-8"></div>
        <Form
          name="register"
          onFinish={onFinish}
          autoComplete="off"
          layout="vertical"
        >
          <Form.Item
            name="username"
            rules={[
              { required: true, message: '' },
              { min: 3, message: '3' },
            ]}
          >
            <Input
              prefix={<UserOutlined />}
              placeholder=""
              size="large"
            />
          </Form.Item>

          <Form.Item
            name="email"
            rules={[
              { required: true, message: '' },
              { type: 'email', message: '' },
            ]}
          >
            <Input
              prefix={<MailOutlined />}
              placeholder=""
              size="large"
            />
          </Form.Item>

          <Form.Item
            name="department"
            rules={[{ required: true, message: '' }]}
          >
            <Input
              prefix={<BankOutlined />}
              placeholder=""
              size="large"
            />
          </Form.Item>

          <Form.Item
            name="password"
            rules={[
              { required: true, message: '' },
              { min: 6, message: '6' },
            ]}
          >
            <Input.Password
              prefix={<LockOutlined />}
              placeholder=""
              size="large"
            />
          </Form.Item>

          <Form.Item
            name="confirmPassword"
            dependencies={['password']}
            rules={[
              { required: true, message: '' },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (!value || getFieldValue('password') === value) {
                    return Promise.resolve();
                  }
                  return Promise.reject(new Error(''));
                },
              }),
            ]}
          >
            <Input.Password
              prefix={<LockOutlined />}
              placeholder=""
              size="large"
            />
          </Form.Item>

          <Form.Item>
            <Button
              type="primary"
              htmlType="submit"
              loading={loading}
              className="w-full"
              size="large"
            >
              
            </Button>
          </Form.Item>

          <div className="text-center">
            
            <Link to="/login"></Link>
          </div>
        </Form>
      </Card>
    </div>
  );
};

export default Register; 