import React from 'react';
import { Outlet, useNavigate, useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { Layout as AntLayout, Menu, Button, Dropdown, Space, Input } from 'antd';
import {
  HomeOutlined,
  CalendarOutlined,
  ClockCircleOutlined,
  ToolOutlined,
  BookOutlined,
  ReadOutlined,
  CarOutlined,
  UserOutlined,
  DownOutlined,
  SearchOutlined,
  PlusOutlined,
} from '@ant-design/icons';

const { Header, Sider, Content } = AntLayout;
const { Search } = Input;

const Layout = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { user, isAuthenticated } = useSelector((state) => state.auth);

  const menuItems = [
    {
      key: '/',
      icon: <HomeOutlined />,
      label: '',
    },
    {
      key: '/meetings',
      icon: <CalendarOutlined />,
      label: '',
    },
    {
      key: '/attendance',
      icon: <ClockCircleOutlined />,
      label: '',
    },
    {
      key: '/tools',
      icon: <ToolOutlined />,
      label: '',
    },
    {
      key: '/standards',
      icon: <BookOutlined />,
      label: '',
    },
    {
      key: '/knowledge',
      icon: <ReadOutlined />,
      label: '',
    },
    {
      key: '/vehicles',
      icon: <CarOutlined />,
      label: '',
    },
  ];

  const userMenuItems = [
    {
      key: 'profile',
      label: '',
    },
    {
      key: 'settings',
      label: '',
    },
    {
      type: 'divider',
    },
    {
      key: 'logout',
      label: '',
    },
  ];

  const handleMenuClick = ({ key }) => {
    navigate(key);
  };

  const handleUserMenuClick = ({ key }) => {
    switch (key) {
      case 'profile':
        navigate('/profile');
        break;
      case 'settings':
        navigate('/settings');
        break;
      case 'logout':
        // action
        navigate('/');
        break;
      default:
        break;
    }
  };

  return (
    <AntLayout className="min-h-screen">
      <Sider width={240} theme="light" className="fixed h-screen shadow-sm">
        <div className="p-6">
          <div className="text-2xl font-bold text-primary"></div>
        </div>
        <Menu
          mode="inline"
          selectedKeys={[location.pathname]}
          items={menuItems}
          onClick={handleMenuClick}
          className="border-none text-lg"
          style={{
            fontSize: '17px',
          }}
        />
      </Sider>
      <AntLayout className="ml-[240px]">
        <Header className="bg-white px-8 py-4 flex items-center justify-between shadow-sm" style={{ height: '80px', lineHeight: 'normal' }}>
          <div className="flex-1 max-w-xl my-2">
            <Search
              placeholder="..."
              allowClear
              size="large"
              className="header-search"
              style={{ width: '100%' }}
            />
          </div>
          <div className="flex items-center gap-4">
            <Button 
              type="primary" 
              icon={<PlusOutlined />} 
              size="large" 
              className="flex items-center text-base h-[42px]"
            >
              
            </Button>
            {isAuthenticated ? (
              <Dropdown
                menu={{
                  items: userMenuItems,
                  onClick: handleUserMenuClick,
                }}
                trigger={['click']}
              >
                <Space className="cursor-pointer text-base h-[42px]">
                  <UserOutlined style={{ fontSize: '18px' }} />
                  <span className="font-medium">{user?.name}</span>
                  <DownOutlined />
                </Space>
              </Dropdown>
            ) : (
              <Button 
                type="link" 
                onClick={() => navigate('/login')} 
                size="large" 
                className="text-base h-[42px]"
              >
                /
              </Button>
            )}
          </div>
        </Header>
        <Content className="p-8 bg-[#f5f5f5]">
          <Outlet />
        </Content>
      </AntLayout>
      <style jsx global>{`
        .ant-menu-item {
          height: 56px !important;
          line-height: 56px !important;
          font-weight: 600 !important;
        }
        .ant-menu-item .anticon {
          font-size: 20px !important;
          font-weight: bold !important;
        }
        .ant-menu-item-selected {
          font-weight: 700 !important;
        }
        .ant-menu-item .ant-menu-item-icon {
          stroke-width: 3 !important;
          stroke: currentColor !important;
        }
        .header-search .ant-input-affix-wrapper {
          height: 42px !important;
          padding: 4px 16px !important;
        }
        .header-search .ant-input-search-button {
          height: 42px !important;
        }
        .header-search .ant-input {
          font-size: 16px !important;
        }
        .header-search .ant-input-prefix {
          font-size: 18px !important;
        }
        .ant-layout-header {
          padding: 16px 32px !important;
        }
      `}</style>
    </AntLayout>
  );
};

export default Layout; 